import React from "react";
import "./card.styles.css";
export function Card(props) {
  return (
    <div className="card-container">
      <img
        src={`https://robohash.org/${props.monster.id}?set=set2`}
        alt="monster"
        className="size"
      />
      <h2>{props.monster.name}</h2>
    </div>
  );
}
